<template>
  <div>
    <div v-if="this.is_weixin()" class="full-box" v-on:click.prevent="showCode('hide')"
         :style="{backgroundImage: 'url(' + bgList[index] + ')',backgroundSize:'cover',backgroundRepeat:'no-repeat',backgroundPosition:'center center'}">
      <p class="title" v-on:click="jump">今日打卡文章:{{title}}</p>
      <div class="protry-box">
        <div class="clear">
          <div class="first">{{protry[1]}}</div>
          <div class="second">{{protry[0]}}</div>
        </div>
      </div>
      <section>
        <div class="error" v-show="isError">{{copyContent}}</div>
        <button class="btn" :data-clipboard-text="copyContent" @click="copyLink">复制</button>
        <div class="tip-box" :class="tip == '复制成功'?'success': tip == '复制失败'?'error':''" v-show="tipShow">{{tip}}</div>
      </section>
      <div class="tip">
        <p>复制并将诗词粘贴回复给百度校园公众号</p>
        <p>即可完成今日打卡</p>
        <div class="guanzhu" v-on:click.stop="showCode('show')">关注</div>
      </div>
      <img :class="erCodeShow?'ershow':'erhide'" src="../../lib/qrCode.png" class="qr-code" alt="">
    </div>
    <div v-if="!this.is_weixin()">
      <p style="font-size:20px;text-align: center;margin-top:3rem;padding-bottom:.1rem">长按识别二维码</p>
      <p style="font-size:20px;text-align: center;margin-bottom:.2rem">关注百度校园公众号</p>
      <img src="../../lib/qrCode.png" alt="">
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  import clipboard from 'clipboard';
  import qs from 'qs'
  import bg1 from '../../assets/qiandao/spring-01.jpg'
  import bg2 from '../../assets/qiandao/spring-02.jpg'
  import bg3 from '../../assets/qiandao/spring-03.jpg'
  import bg4 from '../../assets/qiandao/spring-04.jpg'
  import bg5 from '../../assets/qiandao/spring-05.jpg'
  import bg6 from '../../assets/qiandao/spring-06.jpg'
  import bg7 from '../../assets/qiandao/spring-07.jpg'
  import bg8 from '../../assets/qiandao/spring-08.jpg'
  export default {
    name: "PoetryPage",
    data() {
      return {
        bgList: [bg1, bg2, bg3, bg4, bg5, bg6, bg7, bg8],
        index: '',
        protry: '',
        copyContent: '',
        Clipboard:null,
        tipShow: false,
        tip: '',
        contentHeight: '',
        title: '',
        articleUrl:'',
        erCodeShow:false,
        isError:false
      }
    },
    mounted() {
      this.forbidden_wx_share();
      if (this.is_weixin()) {
        this.index = Math.floor(Math.random() * 7);
        if(this.getQueryParams('r')){
          let r = {
            qrCode:this.getQueryParams('r')
          }
          this.getPotry(r)
        }else{
          this.getPotry(null)
        }
      } else {
        alert('请在微信内打开!')
      }
    },
    methods: {
      getPotry(r){
        axios.post(this.pc_proxy + 'queryPeo.do',r==null?{}:qs.stringify(r)).then(res => {
          if (res.status === 200) {
            if(res.data.newQrCode !== '0'){
              let data = res.data.peo;
              if(data){
                data = data.split('，');
                this.protry = data;
                this.copyContent = res.data.peo;
                this.articleUrl = res.data.articleUrl;
                this.title = res.data.title;
                this.Clipboard = new clipboard('.btn');
              }
            }else{
              window.location.href = res.data.articleUrl +'?time='+((new Date()).getTime());
              // location.reload();
            }
          }
        })
      },
      copyLink() {
        this.Clipboard.on('success', (res) => {
          this.tip = '复制成功'
          this.tipShow = true;
        });
        this.Clipboard.on('error', (res) => {
          this.tip = '复制失败,请手动复制!'
          this.isError = true;
          this.tipShow = true;
        });
        this.disappear();
      },
      disappear() {
        setTimeout(() => {
          this.tipShow = false;
        }, 500)
      },
      showCode(str,e){
        if(str == 'hide'){
          this.erCodeShow = false;
        }
        if(str == 'show'){
          this.erCodeShow = true;
        }
      },
      jump(){
        window.location.href = this.articleUrl;
      }
    }
  }
</script>

<style scoped lang="stylus">
  .full-box {
    position fixed
    top 0
    bottom 0
    left 0
    right 0
    margin auto
    .title {
      width 90%
      font-size .28rem
      text-align center
      color #eef2c6
      text-decoration underline
      padding:.3rem 0
      text-overflow ellipsis
      white-space nowrap
      overflow hidden
      margin 0 auto
    }
    .protry-box {
      position: relative;
      left: 50%;
      float left
      height 100%
      .clear {
        position relative
        right 50%
        padding .2rem
        overflow hidden
        background rgba(0, 0, 0, .3)
        top 10%
        > div {
          padding .2rem
          text-align center
          color #fff
          /*-webkit-writing-mode: vertical-rl;*/
          /*writing-mode: vertical-rl;*/
          float right
          width .8rem
          font-size .7rem
        }
        .second {
          margin-top 1rem
        }
      }
    }
    .tip {
      position absolute
      bottom .5rem
      left 0
      right 0
      margin-left auto
      margin-right: auto
      p {
        text-align center
        color #ffffff
        font-size .28rem
      }
      .guanzhu{
        width .7rem
        height .7rem
        border-radius 50%
        background #666
        font-size 12px
        color #ffffff
        text-align center
        line-height .7rem
        position absolute
        top 0
        bottom 0
        margin-top auto
        margin-bottom auto
        right .4rem
      }
    }
    .tip-box {
      width 2rem
      height .5rem
      line-height .5rem
      font-size .18rem
      color #fff
      background #dc4854
      position absolute
      top 0rem
      left 0
      right 0
      margin-left auto
      margin-right auto
      text-align center
      border-radius 4px
    }
    section {
      position absolute
      bottom 1.5rem
      right 0
      left 0
      margin-left auto
      margin-right auto
      button {
        background #dc4854
        padding: 5px 10px
        border none
        color #ffffff
        display block
        border-radius 4px
        margin 0 auto
      }
      .error{
        font-size .28rem
        color #ffffff
        text-align center
      }
    }
    .qr-code{
      height 3rem
      display block
      position absolute
      left 0
      right 0
      bottom 0
      margin-left auto
      margin-right auto
      transform translateY(3rem)
      transition all .5s
    }
    .ershow{
      transform translateY(0rem)
    }
  }
</style>



